'use strict';
import React from 'react';
import ReactDOM from 'react-dom';
import { default as __links } from 'sources/links';

require('styles/pages/contact.less');

const Link = React.createClass({
  render() {
    return (
      <li className="link-item list-item-icon">
        <div className="list-item-icon__icon">
          <a href={ this.props.url } target="_blank">
            <i className={`icon ${this.props.icon}`}></i>
          </a>
        </div>
      </li>
    );
  }
});

export default React.createClass({
  getInitialState() {
    return { links: __links };
  },
  componentDidMount() {
    const links = ReactDOM.findDOMNode(this.refs.links);
    links.style.display = 'block';
  },
  render() {
    const links = __links.map((result, i) => {
      return <Link key={ result.name } url={ result.url } icon={ result.icon } />;
    });

    return (
      <div ref="links" className="links">
        <ul className="link-items list-icons">
          { links }
        </ul>
      </div>
    );
  }
});


